body {
    font-family: Florence, cursive;
    background: #1f1f11;
    color: sandybrown;
}

#main h1{
    color:sandybrown;
    text-align: center;
}

#main {
    padding: 0;
    box-sizing: border-box;
    width: 60%;
    height: 100%;
}

#book-list {
    padding: 0;
}

#book-list li {
    display: inline-block;
    margin: 12px;
    padding: 10px;
    border-radius: 4px;
    border: 1px solid sandybrown;
    box-shadow: 1px 2px 3px rgba(0,0,0,0.3);
    cursor: pointer;
    color: sandybrown;
}

form{
    background: #fff;
    padding: 20px;
    position: fixed;
    left:0;
    bottom:0;
    width: 400px;
}

form .field {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
}

form label {
    text-align: right;
    padding: 6px;
    color: black;
}

form select, form input {
    margin: 4px;
    padding: 6px;
    box-sizing: border-box;
}

form button {
    color: white;
    font-size: 2em;
    background: #aa1111;
    border: 0;
    padding: 0 10px;
    border-radius: 50%;
    cursor: pointer;
    position: absolute;
    bottom: 10px;
    left: 10px;
}

#book-details {
    position: fixed;
    top:0;
    right: 0;
    width: 40%;
    height: 100%;
    background: #AA1111;
    padding: 30px;
    overflow: auto;
    box-shadow: -2px -3px 5px rgba(0,0,0,0.3);
    box-sizing: border-box;
    color: white;
}